@media (max-width: 415px) {

  @-webkit-keyframes slideUpLeft {
    0% {
      opacity: 1;
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  
    100% {
      opacity: 0;
      -webkit-transform: translateX(50%);
              transform: translateX(50%);
    }
  }

  @keyframes slideUpLeft {
    0% {
      opacity: 1;
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  
    100% {
      opacity: 0;
      -webkit-transform: translateX(50%);
              transform: translateX(50%);
    }
  }
  
  @-webkit-keyframes SlideUpFromRight {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
    }
  
    100% {
      opacity: 1;
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }
  
  @keyframes SlideUpFromRight {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
    }
  
    100% {
      opacity: 1;
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }
  
  .RightViewParent {
    height: 100vh;
    overflow: hidden;
    width: 100%;
  }

  .RighViewContent { 
    -webkit-animation: slideUpLeft 1s ease-in-out 6s; 
            animation: slideUpLeft 1s ease-in-out 6s;
    height: 100vh;
    width: 100vw;
  }

  .ImageParent {
    -webkit-animation: SlideUpFromRight 1s ease-out 0s;
            animation: SlideUpFromRight 1s ease-out 0s;
    width: 100vw;
  }

  .Images {
    width: 70%;
  }

  .ProjectButtons {
    -ms-flex-direction: column;
        flex-direction: column;
    height: 60px;
    width: 60%
  }
  
  .ProjectLinkButtons {
    margin: 5px 0;
  }

}


