@-webkit-keyframes slideUpTop {
	0% {
		opacity: 1;
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}

	100% {
		opacity: 0;
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
	}
}

@keyframes slideUpTop {
	0% {
		opacity: 1;
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}

	100% {
		opacity: 0;
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
	}
}

@-webkit-keyframes SlideUpFromBottom {
	0% {
		opacity: 0;
		-webkit-transform: translateY(50%);
		        transform: translateY(50%);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
}

@keyframes SlideUpFromBottom {
	0% {
		opacity: 0;
		-webkit-transform: translateY(50%);
		        transform: translateY(50%);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0%);
		        transform: translateY(0%);
	}
}

.RightViewParent {
  background-color: #F5D0C5;
  height: 100vh;
  overflow: hidden;
  width: 40%;
}

.RighViewContent { 
  -webkit-animation: slideUpTop 1s ease-in-out 6s; 
          animation: slideUpTop 1s ease-in-out 6s;
  height: 100vh;
  position: absolute;
  width: 100vw;
  z-index: 6;
}

.ImageParent {
  -webkit-animation: SlideUpFromBottom 1s ease-out 0s;
          animation: SlideUpFromBottom 1s ease-out 0s;
  -ms-flex-align: center;
      align-items: center; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-direction: column; 
      flex-direction: column;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  width: 40%;
  z-index: 3;
}

.Images {
  border-radius: 5px;
  height: 70%;
  width: 50%;
}

.ProjectButtons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 45px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 5px 0;
  width: 50%
}

.ProjectLinkButtons {
  background: #fff;
  border-radius: 5px;
  outline: 0;
  border: none;
}

.ProjectLinkButtons:active {
  background: #E5E5E5;
  cursor: pointer; 
}

.ProjectLinkButtons:hover {
  cursor: pointer; 
}

